.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 450px;
  z-index: 30;
  text-align: center;
  background-color: #9fdefc;
  border-radius: 4px;
  padding: 30px;
  font-family: Open Sans;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 15px;
  height: 15px;
  fill: #444;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

